// Get the main content area where pages will be displayed
const mainContent = document.getElementById('main-content'); // Make sure you have this element in your HTML

// Define routes (mapping paths to components or functions)
const routes = {
  '/': () => { return document.createElement('my-home-page'); }, // Web Component
  '/about': () => { return document.createElement('my-about-page'); }, // Web Component
  '/contact': () => {
      // Example with a function and no web component
      const contactPage = document.createElement('div');
      contactPage.innerHTML = "<p>Contact us at info@example.com</p>";
      return contactPage;
  },
  '/products/:id': (params) => {
      const productPage = document.createElement('div');
      productPage.textContent = `Product ID: ${params.id}`;
      return productPage;
  },
  '*': () => {  // Catch-all for 404s
      const notFoundPage = document.createElement('div');
      notFoundPage.textContent = "404: Page Not Found";
      return notFoundPage;
  }
};

function handleNavigation(url) {
  const path = url.pathname;
  const params = {};

  // Extract path parameters (e.g., /products/123)
  const pathParts = path.split('/').filter(part => part !== '');
  if (pathParts.length > 1 && pathParts[0] === 'products') {
      params.id = pathParts[1]; // Get the ID
  }

  const routeHandler = routes[path] || routes['*']; // Get handler or 404
  const pageContent = routeHandler(params); // Call the handler, pass the parameters
  mainContent.innerHTML = ''; // Clear existing content
  mainContent.appendChild(pageContent); // Add the new content

  // Update browser history (important for back/forward buttons)
    window.history.pushState(null, '', url); // Update the URL in the address bar
}


// Initial navigation on page load
handleNavigation(window.location);

// Intercept link clicks (prevent full page reload and handle routing)
document.addEventListener('click', (event) => {
  if (event.target.tagName === 'A') {
    event.preventDefault(); // Prevent default link behavior
    const href = event.target.getAttribute('href');
    const url = new URL(href, window.location.origin); // Create a full URL object
    handleNavigation(url);
  }
});

// Handle browser back/forward buttons
window.addEventListener('popstate', () => {
  handleNavigation(window.location);
});